import ExerciseSet from './ExerciseSet';
import Exercise from './Exercise';
import Utils from '@/services/Utils';

export default class RoutineExercise {
    public id: string;
    public show: boolean = false;
    public sets: ExerciseSet[];

    public constructor(exercise?: Exercise, sets?: number, id?: string) {
        this.id = id !== undefined ? id : Utils.Guid();
        this.sets = [];

        if (exercise !== undefined && sets !== undefined) {
            for (let index = 0; index < sets; index++) {
                const tempExercise = {...exercise} as Exercise;
                tempExercise.id = Utils.Guid();

                this.sets.push(new ExerciseSet(tempExercise));
            }
        }
    }

    public get name() {
        if (this.sets.length > 0) {
            return this.sets[0].exercise.name;
        }

        return ''
    }
}
