import Exercise from '@/models/Exercise';
import Utils from '@/services/Utils';

export default class ExerciseSet {
    public exercise: Exercise;

    public constructor(exercise: Exercise) {
        for (let index = 0; index < exercise.constraints.length; index++) {
            const tempConstraint = {...exercise.constraints[index]}
            tempConstraint.id = Utils.Guid(); 

            exercise.constraints.splice(index, 1, tempConstraint);
        }

        this.exercise = exercise;
    }
}
